import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useData } from '../context/DataContext';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  Alert
} from '@mui/material';
import SchoolIcon from '@mui/icons-material/School';

export default function LoginPage() {
  const [login, setLogin] = useState('');
  const [senha, setSenha] = useState('');
  const [erro, setErro] = useState('');
  const { login: fazerLogin } = useData();
  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    setErro('');
    
    if (fazerLogin(login, senha)) {
      navigate('/');
    } else {
      setErro('Login ou senha invlidos');
    }
  };

  return (
    <Container maxWidth="sm">
      <Box
        sx={{
          minHeight: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center'
        }}
      >
        <Paper elevation={3} sx={{ p: 4, width: '100%' }}>
          <Box sx={{ textAlign: 'center', mb: 3 }}>
            <SchoolIcon sx={{ fontSize: 60, color: 'primary.main', mb: 2 }} />
            <Typography variant="h4" gutterBottom>
              CEI
            </Typography>
            <Typography variant="subtitle1" color="text.secondary">
              Controle Escolar Inteligente
            </Typography>
          </Box>

          {erro && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {erro}
            </Alert>
          )}

          <form onSubmit={handleSubmit}>
            <TextField
              label="Login"
              fullWidth
              margin="normal"
              value={login}
              onChange={(e) => setLogin(e.target.value)}
              required
            />
            <TextField
              label="Senha"
              type="password"
              fullWidth
              margin="normal"
              value={senha}
              onChange={(e) => setSenha(e.target.value)}
              required
            />
            <Button
              type="submit"
              variant="contained"
              fullWidth
              size="large"
              sx={{ mt: 3 }}
            >
              Entrar
            </Button>
          </form>

          <Box sx={{ mt: 2, textAlign: 'center' }}>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              Sua instituio ainda no est cadastrada?
            </Typography>
            <Button
              variant="outlined"
              fullWidth
              onClick={() => navigate('/cadastro-escola')}
            >
              Cadastrar Nova Instituio
            </Button>
          </Box>
          
          {/* Marca Registrada */}
          <Box sx={{ mt: 3, pt: 2, borderTop: 1, borderColor: 'divider', textAlign: 'center' }}>
            <Typography variant="caption" color="text.secondary" display="block">
               {new Date().getFullYear()} CEI - Sistema desenvolvido por
            </Typography>
            <Typography variant="caption" color="primary" display="block" fontWeight="bold">
              Wander Pires Silva Coelho 
            </Typography>
            <Typography variant="caption" color="text.secondary" display="block">
              Todos os direitos reservados
            </Typography>
          </Box>
        </Paper>
      </Box>
    </Container>
  );
}
