import React, { useState } from 'react';
import { useNavigate, Outlet } from 'react-router-dom';
import { useData } from '../context/DataContext';
import {
  Box,
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  IconButton,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Avatar,
  Menu,
  MenuItem
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import DashboardIcon from '@mui/icons-material/Dashboard';
import MenuBookIcon from '@mui/icons-material/MenuBook';
import BusinessIcon from '@mui/icons-material/Business';
import PeopleIcon from '@mui/icons-material/People';
import SwapHorizIcon from '@mui/icons-material/SwapHoriz';
import AssessmentIcon from '@mui/icons-material/Assessment';
import SearchIcon from '@mui/icons-material/Search';
import LogoutIcon from '@mui/icons-material/Logout';
import SchoolIcon from '@mui/icons-material/School';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import SettingsIcon from '@mui/icons-material/Settings';

const drawerWidth = 240;

export default function Layout({ children }) {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const { usuarioLogado, logout, instituicoes } = useData();
  const navigate = useNavigate();

  // Construir menu dinmico baseado no perfil do usurio
  const getMenuItems = () => {
    const baseItems = [
      { text: 'Dashboard', icon: <DashboardIcon />, path: '/' },
      { text: 'Livros', icon: <MenuBookIcon />, path: '/livros' },
      { text: 'Patrimnio', icon: <BusinessIcon />, path: '/patrimonio' },
      { text: 'Clientes', icon: <PeopleIcon />, path: '/clientes' },
      { text: 'Emprstimos', icon: <SwapHorizIcon />, path: '/emprestimos' },
      { text: 'Busca', icon: <SearchIcon />, path: '/busca' },
      { text: 'Relatrios', icon: <AssessmentIcon />, path: '/relatorios' },
    ];

    // Se for Super Admin, adicionar opes exclusivas
    if (usuarioLogado?.perfil === 'SuperAdmin') {
      baseItems.unshift({
        text: 'Gesto Financeira',
        icon: <AccountBalanceWalletIcon />,
        path: '/financeiro-admin'
      });
      baseItems.unshift({
        text: 'Configurar Planos',
        icon: <SettingsIcon />,
        path: '/configurar-planos'
      });
      baseItems.unshift({
        text: 'Gerenciar Escolas',
        icon: <SchoolIcon />,
        path: '/gerenciar-escolas'
      });
    } else {
      // Para escolas, adicionar menu financeiro
      baseItems.splice(1, 0, {
        text: 'Financeiro',
        icon: <AccountBalanceWalletIcon />,
        path: '/financeiro'
      });
    }

    return baseItems;
  };

  const menuItems = getMenuItems();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const handleNavigation = (path) => {
    navigate(path);
    setMobileOpen(false);
  };

  const drawer = (
    <Box>
      <Toolbar>
        <Typography variant="h6" noWrap>
          CEI
        </Typography>
      </Toolbar>
      <List>
        {menuItems.map((item) => (
          <ListItem key={item.text} disablePadding>
            <ListItemButton onClick={() => handleNavigation(item.path)}>
              <ListItemIcon>{item.icon}</ListItemIcon>
              <ListItemText primary={item.text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );

  return (
    <Box sx={{ display: 'flex' }}>
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
            Controle Escolar Inteligente
          </Typography>
          <IconButton onClick={handleMenu} color="inherit">
            <Avatar sx={{ width: 32, height: 32 }}>
              {usuarioLogado?.nome?.charAt(0)}
            </Avatar>
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleClose}
          >
            <MenuItem disabled>
              <Typography variant="body2">{usuarioLogado?.nome}</Typography>
            </MenuItem>
            <MenuItem onClick={handleLogout}>
              <ListItemIcon>
                <LogoutIcon fontSize="small" />
              </ListItemIcon>
              Sair
            </MenuItem>
          </Menu>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
      >
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{ keepMounted: true }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column'
        }}
      >
        <Toolbar />
        <Box sx={{ flexGrow: 1 }}>
          {children}
        </Box>
        
        {/* Rodap com Marca Registrada */}
        <Box 
          component="footer" 
          sx={{ 
            mt: 4, 
            py: 2, 
            borderTop: 1, 
            borderColor: 'divider',
            textAlign: 'center'
          }}
        >
          <Typography variant="body2" color="text.secondary">
             {new Date().getFullYear()} CEI - Controle Escolar Inteligente
          </Typography>
          <Typography variant="caption" color="text.secondary">
            Sistema desenvolvido e patenteado por <strong>Wander Pires Silva Coelho</strong>
          </Typography>
          <Typography variant="caption" color="text.secondary" display="block">
            Todos os direitos reservados 
          </Typography>
        </Box>
      </Box>
    </Box>
  );
}
